.card {
  margin-bottom: 50px;
}

.card-content {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-content: center;

  z-index: 20;
}

.card-features,
.card-purchase {
  width: 350px;

  padding: 30px 20px 20px;
  background: linear-gradient(153.9deg, #B596DD 0%, #20B8E0 100%);
}

.card-features {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  min-height: 310px;

  .feature-item {
    display: flex;
    justify-content: space-between;

    margin-bottom: 16px;
    font-size: 14px;

    .name {
      font-weight: bold;
      //display: inline-block;
      width: 50%;
    }

    .value {
      text-align: right;
      //display: inline-block;
      //margin-right: 4px
      width: 50%;
    }
  }
}

.card .headline {
  font-size: 48px;
}

.sizes {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;

  margin-bottom: 30px;
}

.size {
  position: relative;
  text-align: center;
  padding-bottom: 50px;

  .number {
    position: relative;

    width: 40px;
    height: 40px;
    border: 1px solid #fff;
    border-radius: 50%;
    line-height: 38px;
    cursor: pointer;

    &:before {
      position: absolute;
      bottom: -12px;
      left: 14px;

      content: "";

      border: 6px solid transparent; border-top: 6px solid #fff;
      opacity: 0;
      pointer-events: none;
      transition: 320ms;
    }
  }

  .value {
    position: absolute;
    bottom: 8px;
    left: -10px;

    margin-top: 20px;
    //margin-right: -5px;
    font-size: 14px;
    width: 60px;
    transform: rotate(-90deg);

    opacity: 0;
    pointer-events: none;
    transition: 320ms;
  }

  &.active {
    .number {
      cursor: auto;
    }

    .number:before {
      opacity: 1;

    }

    .value {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.colors-wrapper {
  display: flex;

  font-weight: bold;
  margin-bottom: 40px;

  .name {

  }
}


.colors {
  margin-left: 20px;
}

.color {
  position: relative;

  display: inline-block;

  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 15px;

  &:before {
    position: absolute;
    top: -4px;
    left: -4px;

    content: "";

    width: calc(100% + 8px);
    height: calc(100% + 8px);

    border-radius: 50%;

    border: 1px solid #fff;
    box-sizing: border-box;
  }
}

.color--white {
  background-color: #fff;
}

.color--black {
  background-color: #000;
}

.card {
  .form-group {
    margin-top: 15px;

    &--btn {
      margin-top: 40px;
    }
  }
}

.form-control:invalid {
  box-shadow: inset 5px 5px 10px rgba(38, 68, 100, 0.3);
}

.form-control {
  display: block;
  width: 100%;
  height: 50px;
  padding: 0 20px;
  color: #8F9DC1;
  font-size: 14px;

  background: #DBEDFF;
  box-shadow: inset 5px 5px 10px rgba(38, 68, 100, 0.3);
  border-radius: 25px;

  &::placeholder {
    color: #8F9DC1;
  }
}

.card {
  position: relative;

  .prices {
    margin-top: 20px;

    &--big {
      .new-price {
        font-size: 72px;
      }

      .old-price {
        font-size: 36px;
      }
    }
  }

  .new-price {
    color: #E461EF;
    font-size: 48px;


  }
}

.card-offer {
  padding: 10px 20px;
  text-align: center;
  font-size: 14px;
  border: 1px solid #fff;
  margin: 20px 10px 0;
  //margin-top: 20px;
  line-height: 1.5;

  span {
    font-weight: bold;
    color: #49FFBE;
  }
}

.card .product {
  //position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;

  height: 100%;

  //opacity: 0;
  //pointer-events: none;

  &:before {
    position: absolute;
    top: 20px;
    left: 0;
    content: "";
    width: 100%;
    height: 367px;

    background: url("../images/round.png") top center no-repeat;
  }

  //&.active {
  //  opacity: 1;
  //  pointer-events: auto;
  //}
}

.card-images {
  position: relative;
  padding-top: 20px;

  width: calc(100% - 360px - 360px);
}

.product {

}

.product-image .image {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 250px;
}

.product-image {
  position: relative;
  //position: absolute;
  //bottom: 45%;
  //left: 0;

  width: 100%;
  text-align: center;
  z-index: 20;

  img {
    //display: block;
    max-width: 100%;
  }
}

.card--multi {
  .color {
    cursor: pointer;

    &.active {
      cursor: auto;
    }
  }

  .product-image {
    .image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      //height: 250px;

      img {
        transform: scale(0.4) rotate(0.001deg);
        opacity: 0;
        pointer-events: none;
        transition: 320ms;
      }

      &.active {
        position: relative;

        img {
          transform: scale(1) rotate(0.001deg);
          opacity: 1;
          pointer-events: auto;
        }
      }
    }
  }
}


.card-galleries {
  position: relative;

}


.card--multi {
  .card-gallery {
    position: absolute;
    bottom: 0;
    left: 0;


    opacity: 0;
    pointer-events: none;

    .item {
      transition: 320ms;

      opacity: 0;
      transform: scale(0.6) rotate(0.001deg);
    }

    &.active {
      position: relative;
      opacity: 1;
      pointer-events: auto;

      .item {
        opacity: 1;
        transform: scale(1) rotate(0.001deg);
      }
    }
  }
}
