.card-gallery {
  display: flex;
  justify-content: space-between;

  width: 100%;

  //position: absolute;
  //bottom: 0;
  //left: 0;

  .item {
    width: 24.3%;



    img {
      display: block;
      width: 100%;
    }
  }


}



.card .sale-text {
  color: #8DC6FF;
  font-family: Montserrat;
  font-size: 36px;
  line-height: 1.4;
  font-weight: bold;
  margin: 20px 0 60px;
}

@media (min-width: 1024px) {

  .card-gallery .item {
    img {
      position: relative;
      top: 0;
      transition: 320ms;
    }

    &:hover img {
      top: -4px;
    }
  }

}

@media (max-width: 1199px) {
  .card-features, .card-purchase {
    width: 300px;
  }


  .card-images {
    width: calc(100% - 300px - 300px - 10px);
  }

  .card .prices--big .new-price {
    font-size: 68px;
  }

  .card .prices--big .old-price {
    font-size: 32px;
  }

  .card .sale-text {

    margin-bottom: 100px;
  }



}

@media (max-width: 991px) {

  .card {
    margin-bottom: 40px;
  }

  .card .sale-text {
    color: #327EC9;
    z-index: 10;
    margin-bottom: 60px;
    margin-top: 0;
  }

  .card-content {
    flex-wrap: wrap;
  }

  .card-features, .card-purchase {
    order: 2;
  }

  .card-images {
    order: 1;
  }

  .card .product {
    justify-content: space-between;
  }

  .card .product:before {
    height: 100%;
    background-size: contain;
  }

  .card .sale-text {
  }

  .card .headline {
    margin-bottom: 10px;
  }

  .promotion-end-date {
    width: 100%;
  }

}


@media (max-width: 991px) and (min-width: 768px) {

  .card-images {
    width: 47%;
  }

  .card-features {
    width: 50%;
  }

  .card-purchase {
    margin-top: 20px;
    width: 100%;
  }

  .card-purchase .order-form {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .sizes {
    width: 47%;
  }

  .colors-wrapper {
    width: 47%;
  }

  .form-group {
    width: 31.5%;
  }

  .card .form-group--btn {
    margin-top: 8px;
  }
}

@media (max-width: 767px) {

  .card-features, .card-purchase {
    order: 2;
    width: 50%;
  }

  .card-images {
    order: 1;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
  }

  .card-features {
    background: linear-gradient(155.9deg, #B596DD 0%, #50acde 100%);
  }

  .card-purchase {
    background: linear-gradient(127.9deg, #79a3de 0%, #32B3DF 100%);

  }

  .card-features,
  .card-purchase {
    width: 100%;

  }

  .card .form-group,
  .sizes,
  .colors-wrapper,
  .card-offer,
  .feature-items {
    width: 100%;
    max-width: 360px;
    margin-left: auto;
    margin-right: auto;
  }

  .card-offer {
    //margin-le
  }

  .card-purchase {
    padding-top: 0;
  }

  //.card-purchase {
  //  padding-right: 40px;
  //  padding-left: 40px;
  //}

}

@media (max-width: 585px) {

  .card-features, .card-purchase {
    position: relative;
    left: -15px;
    min-width: calc(100% + 30px);
  }

  .card-purchase {
    padding-bottom: 30px;
  }

  .product-image {
    height: 61vw;
  }

  .card .product:before {
    background-image: url("../images/round-2.png");

  }

  .product-image img {
    width: 90%;
    //height: 80%;
  }

  .card .sale-text {
    margin: -1vw 0 8vw;
  }

  .card .headline {
    margin-bottom: -20px;
  }

  .card-images {
    //padding-top-top: 0;
    width: 100%;
  }

  .card-gallery {
    position: relative;
    z-index: 10;
  }

  .card .prices {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;

    margin-top: 0;
  }


  .card .old-price,
  .card .prices--big .old-price {
    font-size: 22px;
  }

  .card .new-price,
  .card .prices--big .new-price {
    color: #B300C2;
    font-size: 64px;
  }

  .card .form-group--btn {
    margin-top: 30px;
  }

  .card {
    //B300C2

  }

  .product-image .image {
    height: 60vw;
  }

}

@media (max-width: 480px) {

  .card-galleries {
    width: 100%;
  }

  .card-gallery {
    left: 8px;
    width: calc(100% + 15px);
    //padding-left: 7px;

    .item {
      display: block;
      min-width: 100%;

    }
  }

}

