.overview {
  position: relative;
  padding-top: 20px;
  padding-bottom: 140px;
}

.overview .headline {
  max-width: 560px;
  margin-left: auto;
  text-align: right;

  font-size: 48px;
}


.overview-gallery {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;

  z-index: 40;

  .item {
    position: relative;
    display: block;
    width: 32%;
    margin-bottom: 25px;

    &:nth-child(2),
    &:nth-child(5) {
      top: 60px;
    }

    &:nth-child(3),
    &:nth-child(6) {
      top: 120px;
    }

    img {
      display: block;
      width: 100%;
    }
  }
}


@media (min-width: 1366px) {
  .overview-gallery {
    left: -55px;
    width: calc(100% + 80px + 30px);
  }
}

@media (min-width: 586px) {

  .overview-gallery {
    .item {
      &:before,
      &:after {
        position: absolute;
        content: "";
        background-color: #fff;
      }

      &:nth-child(1):before {
        top: -3.5%;
        right: -3%;
        width: 1000px;
        height: 1px;
      }

      &:nth-child(1):after {
        top: -11%;
        right: -3%;
        width: 1px;
        height: calc(200% + 34%);
      }

      &:nth-child(2):before {
        bottom: -2.3%;
        right: -3.3%;
        width: calc(100% + 6.6%);
        height: 1px;
      }

      &:nth-child(3):before {
        top: -3.5%;
        left: -3%;
        width: 1000px;
        height: 1px;
      }

      &:nth-child(3):after {
        top: -18.5%;
        left: -3%;
        width: 1px;
        height: calc(200% + 34%);
      }

      &:nth-child(4):before {
        top: -2.3%;
        right: -3%;
        width: 1000px;
        height: 1px;
      }

      &:nth-child(4):after {
        bottom: -3.5%;
        right: -3%;
        width: 1000px;
        height: 1px;
      }

      &:nth-child(6):before {
        top: -2%;
        left: -3%;
        width: 1000px;
        height: 1px;
      }

      &:nth-child(6):after {
        bottom: -3.5%;
        left: -3%;
        width: 1000px;
        height: 1px;
      }

    }
  }
}

@media (min-width: 1024px) {

  .overview-gallery {
    .item span {
      position: absolute;
      top: 0;
      left: 0;

      display: block;
      width: 100%;
      height: 100%;

      &:before {
        position: absolute;
        top: 0;
        left: 0;

        content: "";

        width: 100%;
        height: 100%;
        background: linear-gradient(155.87deg, rgba(69, 243, 189, 0.7) 0%, rgba(197, 74, 251, 0.7) 100%);
        backdrop-filter: blur(0px);
        z-index: 20;
        pointer-events: none;
        opacity: 0;

        transition: 640ms;
      }

      &:after {
        position: absolute;
        top: 0;
        left: 0;

        content: "";

        width: 100%;
        height: 100%;
        background: url("../images/icon-hover.png") center no-repeat;

        opacity: 0;
        pointer-events: none;
        transition: 640ms;

        z-index: 30;
      }

      &:hover:before {
        opacity: 1;
        backdrop-filter: blur(10px);
      }

      &:hover:after {
        opacity: 1;
      }
    }
  }
}

@media (max-width: 1365px) {



}

@media (max-width: 1199px) {
  .overview-gallery .item {
    margin-bottom: 15px;
  }
}

@media (max-width: 991px) {



}

@media (max-width: 767px) {

  .overview .headline {
    font-size: 36px;
  }

  .overview-gallery .item{
    margin-bottom: 12px;
  }


}


@media (max-width: 767px) and (min-width: 586px) {
  .overview .headline {
    max-width: 400px;
  }
}

@media (max-width: 585px) {
  .overview {
    padding-bottom: 15px;
  }

  .overview .headline {
    text-align: center;
    margin-bottom: 30px;

  }

  .overview-gallery {
    max-width: 415px;
    width: 100%;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;

    &:before {
      position: absolute;
      top: -10px;
      left: calc(50% - 500px);
      content: "";
      width: 1000px;
      height: 1px;
      background-color: #fff;
    }

    &:after {
      position: absolute;
      bottom: 20px;
      left: calc(50% - 500px);
      content: "";
      width: 1000px;
      height: 1px;
      background-color: #fff;
    }

    .owl-dots {
      margin-top: 10px;
    }
  }

  .overview-gallery .item {
    width: 100%;
    //margin-bottom: 0;
  }



}

@media (max-width: 480px) {

  .overview .headline {
    font-size: 27px;
  }

}

