.privilege {
  padding-top: 60px;
  //border-left: 1px solid #fff;
}


.privilege .headline {

}

.privilege-item {
  position: relative;
  padding: 25px 25px 15px 15px;

  border: 1px solid #fff;
  background: rgba(179, 0, 194, 0.4);
  max-width: 360px;
  width: 100%;
  margin-top: 54px;

  .head {
    text-transform: uppercase;
    color: #49FFBE;
    font-size: 24px;
    margin-bottom: 10px;

    font-family: "Bebas Neue Cyrillic";
  }

  .text {
    font-size: 14px;
    line-height: 1.4;
  }
}

.privilege .headline {
  font-size: 48px;
}

.privilege-items {
  position: relative;

  margin-top: 110px;
  margin-bottom: 50px;

  &:before {
    position: absolute;
    top: -190px;
    right: -100px;
    content: "";

    width: 561px;
    height: 593px;

    background: url("../images/priv-girl.png") center no-repeat;
    background-size: contain;
  }

}

.privilege-content {
  padding-bottom: 75px;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}

@media (min-width: 992px) {
  .privilege-item {
    min-width: 173px;
  }

  .privilege-item:nth-child(1),
  .privilege-item:nth-child(3) {
    left: -1px;
    border-top-right-radius: 100px;
    border-bottom-right-radius: 100px;
  }

  .privilege-item:nth-child(2) {
    position: absolute;
    top: 68px;
    left: 380px;

    text-align: center;
    border-radius: 100px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.privilege .order-link {
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 1365px) {

  .privilege-items:before {
    top: -90px;
    right: -30px;

    width: 461px;
    height: 493px;
  }

}

@media (max-width: 1199px) {
  .privilege {
    padding-bottom: 40px;
  }

  .privilege-items {
    margin-top: 80px;
  }

  .privilege-item {
    max-width: 310px;
  }

  .privilege-items:before {
    top: 0;
    right: -10px;
    width: 391px;
    height: 413px;
  }

  .privilege-item:nth-child(2) {
    left: 300px;
  }

  .privilege-content {
    padding-bottom: 45px;
  }

}

@media (max-width: 991px) {

  .privilege-item {
    left: -1px;
  }

  .privilege-item:nth-child(2) {
    position: relative;
    left: auto;
    top: auto;
    right: auto;
    bottom: auto;
  }

  .privilege-item {
    border-radius: 100px;
    padding: 25px;
    text-align: center;
    margin-left: 15px;
  }

}

@media (max-width: 991px) and (min-width: 768px) {
  .privilege-item:nth-child(3) {
    //max-width: calc(100% + 2px);
    //width: calc(100% + 2px);

    max-width: 80%;
    margin-right: auto;
    margin-left: auto;
  }

}





@media (max-width: 767px) {

  .privilege-items {
    margin-top: 30px;
    padding-bottom: 480px;
    margin-bottom: 25px;
  }

  .privilege-item {
    left: 0;
    margin-left: 0;
    max-width: 100%;
    margin-top: 20px;

    min-height: 140px;
  }

  .privilege-items:before {
    top: auto;
    bottom: 0;
    width: 100%;
    height: 500px;
    z-index: 10;

    background-position: right center;
  }

}

@media (max-width: 585px) {

  .privilege .headline {
    font-size: 36px;
    padding-left: 8px;
    padding-right: 8px;
  }

  .privilege-content {
    padding-bottom: 10px;
  }

  .privilege .order-link {
    max-width: 360px;
    width: calc(100% - 31px);
  }
}

@media (max-width: 480px) {
  .privilege .headline {
    font-size: 27px;
  }

  .privilege-item {
    margin-top: 10px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .privilege-item .head {
    font-size: 18px;
  }

  .privilege-items {
    padding-bottom: 85vw;
  }

  .privilege-items:before {
    width: 90vw;
    height: 90vw;
  }
}

