.spray {
  position: relative;
  padding-top: 80px;
  padding-bottom: 40px;

  &:after {
    position: absolute;
    top: -30px;
    left: calc(50% - -120px);
    content: "";

    width: 361px;
    height: 244px;

    background: url("../images/spray-dec.png") center no-repeat;
    background-size: contain;
  }
}

.spray-content {
  position: relative;
  background: linear-gradient(96.98deg, rgba(179, 0, 194, 0.5) 0%, rgba(51, 103, 237, 0.5) 100%);
  padding: 20px 123px 30px;

  z-index: 20;

  &:before {
    position: absolute;
    top: -30px;
    right: -160px;
    content: "";

    width: 463px;
    height: 454px;

    background: url("../images/decor-3.png") center no-repeat;
    background-size: contain;
  }


}

.spray .row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.spray-image {
  position: relative;
  width: 280px;

  &:before {
    position: absolute;
    top: -110px;
    right: -34px;
    content: "";

    width: 418px;
    height: 302px;
    background: url("../images/decor-1.png") center no-repeat;
    background-size: contain;
  }

  &:after {
    position: absolute;
    top: 84px;
    left: -194px;
    content: "";

    width: 378px;
    height: 294px;
    background: url("../images/decor-2.png") center no-repeat;
    background-size: contain;
  }

  .image-1 {
    position: relative;
    top: -33px;
    left: 60px;
    margin-top: -30px;
    width: 80px;
  }

  .image-2 {
    position: relative;

    top: -5px;
    left: 40px;

    margin-top: -80px;
    width: 85px;
  }

  img {

  }
}

.spray-desc {
  width: 500px;

  p {
    margin-bottom: 15px;
    font-size: 14px;
  }
}

.spray .headline {
  font-size: 36px;
  margin-bottom: 20px;
}

.spray-feature {
  margin-bottom: 15px;

  .name {
    margin-right: 5px;
    font-weight: bold;
  }
}

.spray .prices {
  position: relative;
  display: inline-block;

  .old-price {
    font-size: 14px;
    font-family: Montserrat;

  }

  .new-price {
    position: absolute;
    top: -10px;
    left: 160px;
    font-size: 48px;
    color: #67F8C4;
  }
}

.spray-form {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;

  .form-group {
    width: 31.5%;

    &--btn {
      margin-left: 8px;
      margin-top: -6px;
    }
  }
}

@media (max-width: 1365px) {

}

@media (max-width: 1199px) {

  .spray:after {
    top: 240px;
    left: calc(50% - -70px);
    width: 261px;
    height: 174px;
  }

  .spray .row {
    justify-content: flex-start;
  }

  .spray-image {
    //width: 300px;
    margin-right: 40px;
  }

  .spray-content:before {
    right: -130px;
    width: 363px;
    height: 354px;
  }

  .spray-image:after {
    top: 104px;
    left: -184px;
    width: 318px;
    height: 254px;
  }

  .spray-image:before {
    //top: -110px;
    width: 348px;
    height: 262px;
  }


  .spray-content {
    padding-left: 30px;
    padding-right: 30px;
  }

}

@media (max-width: 991px) {

  .spray {
    padding-top: 40px;
  }

  .spray:after {
    display: none;
  }

  .spray .row {
    align-items: center;
  }

  .spray-content {
    padding-left: 20px;
    padding-right: 20px;
  }

  .spray-desc {
    width: 310px;
  }

  .spray-image {
    width: 250px;
    margin-right: 20px;
  }

  .spray-image:after {
    top: 174px;
    left: -104px;

    width: 278px;
    height: 214px;
  }

  .spray-image .image-1 {
    top: 0;
  }

  .spray-image .image-2 {
    top: 30px;
  }
  .spray-image:before {
    width: 328px;
    height: 232px;
  }

  .spray-form {
    margin-top: 40px;
  }

  //.spray-form {
  //  flex-wrap: wrap;
  //
  //  .form-group--field {
  //    width: 45%;
  //  }
  //}


}

@media (max-width: 767px) {

  .spray {
    padding-top: 90px;
  }

  .spray .row {
    display: block;
  }

  .spray-image {
    margin-top: -95px;
    margin-bottom: 70px;

    text-align: center;
    width: 100%;

  }

  .spray-image:before {
    top: -20px;

    right: calc(50% - 150px);
  }

  .spray-image:after {
    top: auto;
    bottom: -63px;
    left: calc(50% - 261px);
  }

  .spray-image {

  }

  .spray-image .image-1 {
    left: 0;
    margin-top: 0;

  }

  .spray-image .image-2 {
    left: 0;
  }

  .spray-desc {
    width: 100%;
    text-align: center;
  }

  .spray .prices .new-price {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;

    display: block;
    width: 100%;
    margin-top: 20px;
    margin-left: 0;
  }

  .spray .prices {
    flex-wrap: wrap;
  }


  .spray-form {
    position: relative;

    display: block;
    max-width: 360px;

    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;

    z-index: 20;

    .form-group {
      width: 100%;
    }

    .form-group--field {
      margin-bottom: 15px;
    }

    .form-group--btn {
      margin-top: 40px;
    }
  }

  .spray-content:before {
    top: auto;
    bottom: -80px;
  }

  .spray-content {
    padding-bottom: 40px;
  }

  .spray-form {
  }

}

@media (max-width: 585px) {

  .spray-content {
    position: relative;
    left: -15px;
    width: calc(100% + 30px);
  }

  .spray-content:before {
    top: auto;
    bottom: -90px;
    right: -173px;
    width: 363px;
    height: 374px;
  }

}

