.header {
  position: relative;
  padding-bottom: 190px;

  //height: 600px;

  .main-logo {
    margin-top: -20px;
  }

  &:before {
    position: absolute;
    top: 50px;
    left: calc(50% - 1070px);
    content: "";

    width: 566px;
    height: 471px;

    background: url("../images/header-decor-left.png") center no-repeat;
    background-size: contain;
    pointer-events: none;
  }

  &:after {
    position: absolute;
    bottom: -120px;
    right: calc(50% - 1080px);
    content: "";


    width: 607px;
    height: 447px;

    background: url("../images/header-decor-right.png") center no-repeat;
    background-size: contain;
    pointer-events: none;
  }
}

@media (min-width: 1921px) {
  .header {
    &:before {
      left: -116px;

    }

    &:after {
      right: -116px;

    }
  }
}

.header-subtitle {
  font-size: 20px;
  text-align: center;
  margin-bottom: 30px;
}

.header {

}

.header-items {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  width: 390px;
}

.header-item {
  text-align: center;
  width: 32%;
  font-size: 14px;
  color: #3367ED;

  .icon {
    display: inline-block;
    width: 70px;
    height: 70px;
    border-radius: 50% ;
    border: 1px solid #3367ED;
    margin-bottom: 10px;
  }

  &:nth-child(1) {
    .icon {
      background: url("../images/icon-1.png") center no-repeat;
    }
  }

  &:nth-child(2) {
    .icon {
      background: url("../images/icon-2.png") center no-repeat;
    }
  }

  &:nth-child(3) {
    .icon {
      background: url("../images/icon-3.png") center no-repeat;
    }
  }
}

.header-content {
  position: relative;
}

.header-images {
  //position: relative;
  position: absolute;
  top: 0;
  left: calc(50% - 320px);

  width: 640px;

  height: 640px;

  padding-top: 9%;
  margin-left: auto;
  margin-right: auto;

  .sale-text {
    position: absolute;
    top: 3%;

    width: 100%;
    text-align: center;
    font-size: 72px;
    font-weight: 300;

    font-family: "Bebas Neue Cyrillic";
    color: transparent;

    //text-shadow: #fff 0 0 1px;

    //color: rgba(0, 0, 0, 1);
    //-webkit-text-stroke: 1px rgba(255, 255, 255, 1);
    //text-shadow: 12px 12px 0 rgb(54 216 237 / 6%);

    //text-shadow: 1px 1px 1px red;

    -webkit-text-stroke: 1px white;
    text-stroke: 1px white;
  }

  .image-1 {
    position: relative;
    z-index: 10;
    display: block;
    width: 100%;

    img {
      position: relative;
      display: block;
      width: 100%;
      z-index: 20;
      animation: animUp 3s infinite;
    }

    &:before {
      position: absolute;
      bottom: -20px;
      left: 0;
      width: 100%;
      height: 16px;
      content: "";
      background: radial-gradient(50% 50% at 50% 50%, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 100%);

      animation: animShadow 3s infinite;
    }
  }

  .round {
    position: absolute;

    border-radius: 50%;
    border: 1px solid #fff;

  }

  .round-1 {
    top: 0;
    left: calc(50% - 31.5%);

    width: 63%;
    height: 63%;
  }

  .round-2 {
    top: 20%;
    left: calc(50% - 41.5%);

    width: 83%;
    height: 83%;
  }
}

@keyframes animUp {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(15px);
  }
}

@keyframes animShadow {
  0%, 100% {
  opacity: 1;
  }

  50% {
  opacity: 0.4;
  }
}

.main-price {



}

.prices {
  display: flex;
  justify-content: center;
  align-items: flex-start;

}


.new-price {
  color: #B300C2;

  font-size: 61px;
  line-height: 1;

}

.old-price {
  position: relative;
  font-weight: bold;
  font-size: 30px;

  line-height: 1.4;

  &:before {
    position: absolute;
    top: 50%;
    left: 0;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #B300C2;
  }
}

.old-price,
.new-price {
  font-family: "Bebas Neue Cyrillic";
  margin: 0 5px;
}

.main-price {
  max-width: 280px;

  .order-link {
    margin-top: 30px;


  }
}

.header-price {
  margin-top: -60px;
  margin-left: auto;
}



.promotion-end-date {
  margin-top: 30px;
  text-align: center;
  font-size: 14px;

  span {
    color: #B300C2;
    font-weight: bold;
  }
}

