  }

  .header:before {
    bottom: -100px;
    top: auto;
    left: calc(50% - 840px);

    width: 700px;
    height: 800px;

    background-image: url("../images/header-decor-left.2.png");
    background-position: right top;
    z-index: -10;
  }

