    right: calc(50% - 674px);

    width: 807px;
    height: 820px;
    background-image: url("../images/header-decor-right.2.png");
    z-index: -10;
  }

  .header-price {
    margin-top: -137px;
    margin-right: auto;
  }

  .main-logo {
    font-size: 90px;
  }

  h1 {
    font-size: 36px;
  }

  .header-items {
    display: none;
  }

  .header-images {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
  }


}

