.footer {
  position: relative;
  padding-top: 40px;
  padding-bottom: 20px;

  &:before {
    position: absolute;
    left: calc(50% - 960px);
    bottom: 0;

    content: "";

    width: 1920px;
    height: 1642px;

    background: url("../images/footer-back.png") center top no-repeat;
    background-size: contain;
    pointer-events: none;
    z-index: -20;

  }
}

@media (min-width: 1920px) {
  .footer:before {
    width: 100%;
    left: 0;
    background-position: top center;
    background-size: cover;
  }
}

.footer-content {
  position: relative;

  &:before {
    position: absolute;
    bottom: -10px;
    left: calc(50% - 552px);

    content: "";

    width: 239px;
    height: 727px;

    background: url("../images/footer-girl-1.png") center bottom no-repeat;
    background-size: contain;
  }

  &:after {
    position: absolute;
    bottom: -10px;
    left: calc(50% - -300px);

    content: "";

    width: 363px;
    height: 727px;

    background: url("../images/footer-girl-2.png") center bottom no-repeat;
    background-size: contain;
  }
}

.footer-headline {
  max-width: 570px;
  margin-left: auto;
  margin-right: auto;

  color: #3367ED;

  font-size: 55px;
  line-height: 1;
}

.footer-price {
  margin: 30px auto 40px;

  .old-price {
    color: #3367ED;
  }
}

.footer-image {
  width: 588px;

  margin-left: auto;
  margin-right: auto;
  margin-bottom: 70px;

  img {
    display: block;
    width: 100%;
  }
}

.order-link--footer {
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
}

.footer .promotion-end-date {
  margin-bottom: 40px;
  color: #3E62C0;

  span {

  }
}

.footer-info {
  text-align: center;
  font-weight: bold;
  color: #3367ED;
  font-size: 14px;

  a {
    position: relative;
    color: #B300C2;

    &:before {
      position: absolute;
      bottom: -1px;
      left: 0;
      content: "";
      width: 0;
      height: 1px;
      background-color: #B300C2;
      opacity: 0;
      transition: 320ms;
    }

    &:hover:before {
      width: 100%;
      opacity: 1;
    }
  }
}

@media (max-width: 1365px) {

  .footer-content:after {
    left: calc(50% - -210px);
  }

}

@media (max-width: 1199px) {
  .footer:before {
    left: auto;
    right: 0;
    width: 1199px;
    background-image: url("../images/footer-back--1199.png");
  }

  .footer-content:after {
    left: calc(50% - -200px);
    width: 293px;
    height: 557px;
  }

  .footer-content:before {
    left: calc(50% - 502px);
    width: 189px;
    height: 587px;
  }

  .footer-image {
    width: 488px;
  }

  .footer-headline {
    font-size: 46px;
  }

}

@media (max-width: 991px) {

  .footer:before {
    width: 992px;
    height: 1359px;
    background-image: url("../images/footer-back--992.png");
    background-position: left bottom;
  }

  .footer-content:before {
    left: calc(50% - 382px);
    width: 159px;
    height: 487px;
  }

  .footer-content:after {
    bottom: 0;
    left: calc(50% - -140px);
    width: 243px;
    height: 487px;
  }

  .footer-image {
    width: 418px;
  }

}

.footer-image--mob {
  display: none;
}

@media (max-width: 767px) {


  .footer-image {
    position: relative;
    width: 80%;

    margin-top: 230px;
    margin-bottom: 40px;
    z-index: 20;
  }

  .footer-content:before {
    left: calc(50% - 332px);
    bottom: 230px;
  }

  .footer-content:after {
    bottom: 220px;
    left: calc(50% - -90px);
    width: 243px;
    height: 487px;
  }

  .footer-image--mob {
    display: block;
  }

  .footer-image--desc {
    display: none;
  }

  .footer-price .old-price {
    font-size: 24px;
  }

}



@media (max-width: 585px) {

  .footer {
    padding-top: 20px;
  }

  .footer-headline {
    font-size: 28px;
  }

  .footer-price {
    margin-top: 20px;
  }

  .footer-image {
    margin-top: 150px;
  }

  .footer-content:before {
    bottom: 190px;
    left: calc(50% - 272px);
  }

  .footer-content:after {
    bottom: 180px;
    left: calc(50% - -30px);
  }

}

@media (max-width: 480px) {

  .footer:before {
    width: 100%;
    height: 1196px;
    background-image: url("../images/footer-back--480.png");
  }

  .footer-image {
    margin-top: 71vw;
  }

  .footer-content:before {
    left: calc(50% - 67vw + 15px);
    width: 42vw;
    height: 126vw;
    background-position: right bottom;
  }

  .footer-content:after {
    left: auto;
    right: calc(50% - 67vw + 15px);
    width: 64vw;
    height: 128vw;
    background-position: left bottom;
  }

  .footer .promotion-end-date {
    margin: 15px auto 20px;
  }

}

@media (max-width: 320px) {

}
