// - font-face --------------------------------------

@font-face {
  font-family: 'Bebas Neue Cyrillic';
  src: url('../fonts/BebasNeueCyrillic.woff2') format('woff2'),
  url('../fonts/BebasNeueCyrillic.woff') format('woff'),
  url('../fonts/BebasNeueCyrillic.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat-Regular.woff2') format('woff2'),
  url('../fonts/Montserrat-Regular.woff') format('woff'),
  url('../fonts/Montserrat-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url('../fonts/Montserrat-Bold.woff2') format('woff2'),
  url('../fonts/Montserrat-Bold.woff') format('woff'),
  url('../fonts/Montserrat-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Jura';
  src: url('../fonts/Jura-Light.woff2') format('woff2'),
  url('../fonts/Jura-Light.woff') format('woff'),
  url('../fonts/Jura-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}



a {
  text-decoration: none;
  color: inherit;
  font-family: inherit;
  border: none;
  outline: none;

  &:active {
    outline: none;
    border: none;
  }
}

button {
  font-family: inherit;
  color: inherit;
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
}

input {
  font-family: inherit;
  color: inherit;
  background: none;
  border: none;
  outline: none;
  -webkit-appearance: none;

  &::placeholder {
    opacity: 1;
  }

  &::placeholder {
    color: inherit;
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

img {
  user-select: none;

  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

select::-ms-expand {
  display: none
}

label.error {
  display: none !important;
}

input::-ms-clear {
  width: 15px;
  height: 15px;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.overflow-hidden {
  position: relative;
  overflow: hidden;
}

.back {
  position: absolute;
  top: 0;
  left: 50%;
  width: 1px;
  height: 100%;
  background: none;

  div {
    position: absolute;

    img {
      display: block;
      //width: 100%;
    }
  }

  //img {
  //  position: absolute;
  //}
}

