  line-height: 1;
}

.order-link {
  position: relative;
  display: block;

  width: calc(100% - 14px);
  margin: 4px;

  line-height: 55px;
  font-size: 36px;
  font-family: "Bebas Neue Cyrillic";
  text-align: center;
  border-radius: 35px;

  //background-color: red;

  background: linear-gradient(95.33deg, #FF3AEB 0%, #CD3FFF 50%, #FF3AEB 100%);
  background-size: 200% 100%;
  background-position: 0 0;
  box-shadow: 0px 7px 7px #95A3D8;

  transition: 640ms;

  &:hover {
    background-position: 100% 0;
  }

  &:before {
    position: absolute;
    top: -8px;
    left: -8px;
    content: "";
    width: calc(100% + 16px);
    height: calc(100% + 16px);
    border: 1px solid #fff;
    border-radius: 35px;
    box-sizing: border-box;
  }
}

.order-link--blue {
  background: linear-gradient(95.33deg, #49FFBE 0%, #3367ED 50%, #339ced 100%);
  background-size: 200% 100%;
  background-position: 0 0;
  //box-shadow: 0px 7px 7px
}
