.reviews {
  padding-top: 40px;
  padding-bottom: 40px;
}

.user-photo {
  position: relative;

  width: 100px;
  height: 100px;

  border-radius: 50%;
  margin-top: -70px;

  margin-left: auto;
  margin-right: auto;

  font-size: 16px;
  font-weight: bold;
  line-height: 20px;
  margin-bottom: 20px;
  border-top: 5px solid rgba(179, 0, 194, 0.5);
  border-right: 5px solid rgba(179, 0, 194, 0.5);

  border-left: 5px solid rgba(179, 0, 194, 0);
  border-bottom: 5px solid rgba(179, 0, 194, 0);
  transform: rotate(-45deg);

  img {
    display: block;
    width: 100%;
    border-radius: 50%;
    transform: rotate(45deg);
  }
}

.review {
  position: relative;
  background: linear-gradient(180deg, rgba(179, 0, 194, 0.5) 0%, rgba(51, 103, 237, 0.5) 100%);

  //width: 31%;
  min-height: 230px;
  color: #fff;

  padding: 20px;
  text-align: center;

  margin-top: 60px;
}

.user-name {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 10px;
}

.user-text {
  font-size: 14px;

}

.owl-dots {
  display: flex;
  justify-content: center;

  margin-top: 20px;

  .owl-dot {
    margin: 0 5px;

    span {
      display: block;

      width: 15px;
      height: 15px;
      border-radius: 50%;
      background: #fff;
    }

    &.active {
      span {
        background-color: #3367ED;
      }
    }

  }
}


@media (max-width: 585px) {

  .owl-dots {

    .owl-dot {
      margin: 0 2px;

      span {
        width: 10px;
        height: 10px;
      }

    }
  }
}

