.about {
  position: relative;
  padding-top: 70px;
  padding-bottom: 70px;

  &:before {
    position: absolute;
    top: 0;
    left: calc(50% - 770px);
    content: "";

    width: 98px;
    height: 100%;

    transform: scale(-1, 1);

    background: url("../images/logo.png") center no-repeat;
    background-size: contain;
  }

  &:after {
    position: absolute;
    top: 0;
    left: calc(50% + 690px);
    content: "";

    width: 98px;
    height: 100%;

    transform: scale(1, -1);

    background: url("../images/logo.png") center no-repeat;
    background-size: contain;
  }
}

.about-content {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.about-items {
  position: relative;
  width: 583px;

  z-index: 20;
}

.about-item {
  min-height: 112px;
  padding: 10px 15px;

  margin-bottom: 50px;

  &:last-child {
    margin-bottom: 0;
  }

  h4 {
    margin: 0 0 10px;
    font-size: 30px;

    max-width: 400px;
    font-weight: normal;
    font-family: "Bebas Neue Cyrillic";
  }

  p {
    max-width: 400px;
    font-size: 14px;
    line-height: 1.4;
  }

  &:nth-child(1) {
    background: linear-gradient(90deg, #14CA3C 0%, rgba(20, 202, 60, 0) 100%);
  }

  &:nth-child(2) {
    background: linear-gradient(90deg, #9966EC 0%, rgba(153, 102, 236, 0) 100%);
  }

  &:nth-child(3) {
    background: linear-gradient(90deg, #00A4BB 0%, rgba(0, 164, 187, 0) 100%);
  }
}

.about-images {
  position: relative;
  text-align: center;

  .image {
    display: inline-block;
  }

  img {
    position: relative;
    z-index: 2;
  }

  .image-1 {

  }

  .image-2 {
    margin-left: 30px;
  }

  .round {
    position: absolute;
    width: 550px;
    height: 550px;
    border-radius: 50%;
    border: 1px solid #fff;

    &--1 {
      top: 0;
      left: -246px;
    }

    &--2 {
      top: 0;
      left: -76px;
    }

    &--3 {
      top: 0;
      left: 100px;
    }

  }
}

@media (max-width: 1600px) {
  .about:before {
    left: calc(50% - 720px);
  }
}


@media (max-width: 1400px) {
  .about:before {
    left: calc(50% - 630px);
  }

  .about:before {
    left: calc(50% - 675px);
  }
}

@media (max-width: 1365px) {

  .about:after {
    left: calc(50% + 490px);
  }

  .about:before {
    top: -310px;
    left: calc(50% - 435px);
    transform: rotate(90deg);
  }
}

@media (max-width: 1199px) {

  .about:after {
    display: none;
  }

  .about:before {
    top: -260px;
    left: calc(50% - 346px);
  }

  .about-item {
    margin-bottom: 20px;
  }

.about-images {
  width: 400px;
  height: 400px;

  .image {
    img {
      display: block;
      height: 100%;
    }
  }

  .image-1 {
    height: 100%;
  }
  .image-2 {
    height: 100%;
  }

  .round {
    width: 100%;
    height: 100%;

    &--1 {
      left: -39%;
    }

    &--2 {
      left: -7%;
    }

    &--3 {
      left: 24%;
    }
  }


}

}

@media (max-width: 991px) {

  .about:before {
   display: none;
    left: calc(50% - 236px);
  }

  .about-images {
    width: 380px;
    height: 380px;
  }

  .about-items {
    width: calc(100% - 380px);
  }

  .about-item p {
    max-width: 250px;
  }

}

@media (max-width: 767px) {

  .about {
    padding-top: 40px;
  }

  .about-content {
    display: block;
  }

  .about-items {

    width: 100%;
    margin-bottom: 50px;
  }

  .about-images {
    left: 29px;
    margin-left: auto;
    margin-right: auto;
  }

  .about-item p {
    max-width: 400px;
  }

}

@media (max-width: 585px) {
  .about {
    padding-top: 15px;
    padding-bottom: 15px;
  }

  .about-images {
    width: 70vw;
    height: 70vw;
    left: 0;
  }


  .about-images .image {
    position: relative;
    left: 4vw;
  }

  .about-images .image-1 {
    height: 100%;
    margin-top: -10%;

  }

  .about-images .image-2 {
    margin-left: 9vw;
    height: 100%;
    margin-top: -10%;
  }

  .about-images .round {
    width: 83%;
    height: 83%;
  }


  .about-images .round--1 {
    left: calc(50% - 50vw);
  }

  .about-images .round--2 {
    left: calc(50% - 29vw);
  }

  .about-images .round--3 {
    left: calc(50% - 8vw);
  }

}

@media (max-width: 480px) {

  .about-items {
    position: relative;
    left: -15px;
    width: calc(100% + 30px);
  }
  .about-item {
    padding: 15px;
    min-height: initial;
    margin-bottom: 15px;
  }

  .about-item h4 {
    font-size: 24px;
  }

}

